.svgsprite {
    display:inline-block;
    fill:currentColor;
    vertical-align:middle;

    &._arrow {
    	width:15px;
    	height:9px;
    }
    &._back {
    	width:24px;
    	height:24px;
    }
    &._basket {
    	width:31px;
    	height:27px;
    }
    &._burger {
    	width:18px;
    	height:13px;
    }
    &._catalog-close {
    	width:14px;
    	height:15px;
    }
    &._close {
    	width:14px;
    	height:15px;
    }
    &._email {
    	width:20px;
    	height:16px;
    }
    &._fire {
    	width:18px;
    	height:21px;
    }
    &._header-users {
    	width:24px;
    	height:27px;
    }
    &._mobile-arrow {
    	width:15px;
    	height:10px;
    }
    &._mobile-burger {
    	width:19px;
    	height:12px;
    }
    &._mobile-email {
    	width:24px;
    	height:24px;
    }
    &._mobile-i1 {
    	width:19px;
    	height:21px;
    }
    &._mobile-phone {
    	width:24px;
    	height:24px;
    }
    &._mobile-user {
    	width:17px;
    	height:19px;
    }
    &._nav-drop {
    	width:10px;
    	height:6px;
    }
    &._phone {
    	width:18px;
    	height:18px;
    }
    &._search {
    	width:18px;
    	height:18px;
    }
}