.header-top{
  background-color: #00A7CD;
  padding-top: 15px;
  padding-bottom: 15px;
  color: #fff;
  b{
    font-weight: 700;
  }
}
.header__btns{
  display: flex;
  align-items: center;
}
.header__link{
  font-size: 14px;
  font-weight: bold;
  margin-right: 30px;
  display: flex;
  align-items: center;
  color: #fff;
  svg{
    margin-right: 5px;
  }
}


.header__enter{
  height: 30px;
  display: flex;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  border-radius: 6px;
  background-color: #F24941;
}

.header-middle{
  padding-top: 40px;
}

.header__logo{
  margin-right: 30px;
}

.header__catalog{
  height: 40px;
  border-radius: 6px;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #00A7CD;
  color: #fff;
  width: 130px;
  cursor: pointer;
  margin-right: 15px;
  svg{
    margin-right: 5px;
    transform: translateY(-1px);
  }
  &.active{
    .header__catalog-close{
      display: block;
    }
    .header__catalog-burger{
      display: none;
    }
  } 
  .header__catalog-close{
    display: none;
  }
}

.header__search{
  flex-grow: 1;
  position: relative;
  margin-right: 30px;
  input{
    width: 100%;
    padding-left: 15px;
    height: 40px;
    border-radius: 6px;
    border: 1px solid #D1D1D1;
  }
}

.search__btn{
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  width: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #00A7CD;
  border-radius: 6px;
}

.header__user{
  text-align: center;
  font-size: 14px;
  margin-right: 15px;
  svg{
    margin: 0 auto 5px;
  }
}

.basket__ico{
  display: flex;
  justify-content: center;
  margin-bottom: 5px;
  svg{
    margin-right: 6px;
  }
}

.basket__number{
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background-color: #F24941;
  font-size: 14px;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header-bottom{
  padding-top: 35px;
}

.nav{
  display: flex;
  justify-content: space-between;
}

.nav__link--drop{
  display: flex;
  justify-content: center;
  align-items: center;
  svg{
    margin-left: 12px;
  }
}
.nav__link--stock{
  display: flex;
  justify-content: center;
  color: #F24941;
  svg{
    margin-left: 5px;
    transform: translateY(-4px);
  }
}

.header__menu{
  position: absolute;
  top: 183px;
  left: 0;
  width: 100%;
  filter: drop-shadow(0px 4px 11px rgba(0, 0, 0, 0.16));
  background-color: #fff;
  z-index: 200;
  display: none;
  &.active{
    display: block;
  }
  &:after{
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: calc((100vw - 1160px) / 2);
    background-color: #F5F5F5;
    height: 100%;
  }
}

.menu__left{
  padding: 30px 0;
  display: flex;
  justify-content: flex-end;
  background-color: #F5F5F5;
  width: 270px;
}

.menu__item{
  padding: 10px 10px;
  border-radius: 12px 0 0 12px;
  margin-bottom: 15px;
  cursor: pointer;
  &.active{
    background-color: #fff;
    color: #00A7CD;
  }
}

.menu__right{
  width: calc(100% - 270px);
  padding: 30px 34px;
}

.menu__tab{
  display: none;
  flex-wrap: wrap;
  &.active{
    display: flex;
  }
}

.menu__col{
  width: calc(33.33% - 30px);
  margin-bottom: 30px;
}

.menu__title{
  font-weight: 700;
  margin-bottom: 15px;
}

.menu__link{
  display: block;
  margin-bottom: 10px;
  line-height: 136%;
}

.header.--mobile{
  display: none;
}


.mobile__menu{
  display: none;
}

.mobile__menu-ico{
  height: 24px;
}

.mobile__catalog{
  position: fixed;
  left: 0;
  top: 0;
  width: 325px;
  box-shadow: 0px 0px 9px rgba(0, 0, 0, 0.1);
  z-index: 2000;
  height: 100%;
  padding: 48px 25px;
  overflow: hidden;
  background-color: #fff;
  transform: translateX(-100%);
  transition: .4s;
  &.active{
    transform: translateX(0);
  }
}

.mobile__catalog-title{
  font-weight: bold;
  font-size: 32px;
  margin-bottom: 38px;
}

.mobile__catalog-close{
  position: absolute;
  top: 58px;
  right: 43px;
}

.mobile__catalog-panel{
  overflow-y: auto;
  height: calc(100vh - 111px);
  background-color: #fff;
  width: 100%;
}

.mobile__catalog-link{
  padding: 10px 10px;
  margin-bottom: 15px;
  padding-right: 40px;
  background: url(../img/arrow-right.svg) no-repeat right 10px top 5px / 24px auto;
  display: block;
  &.active{
    background-color: #F5F5F5;
    color: #00A7CD;
  }
}

.mobile__catalog-second-tab{
  display: none;
  &.active{
    display: block;
  }
}
.mobile__catalog-third-tab{
  display: none;
  &.active{
    display: block;
  }
}

.mobile__catalog-back{
  font-weight: 500;
  color: #00A7CD;
  display: flex;
  align-items: center;
  margin-bottom: 15px;
  svg{
    margin-right: 5px;
  }
}

.mobile__catalog-second{
  position: absolute;
  top: 111px;
  transform: translateX(140%);
  transition: .5s;
  z-index: 10;
  width: calc(100% - 30px);
  &.active{
    transform: translateX(0);
  }
}
.mobile__catalog-third{
  position: absolute;
  top: 111px;
  transform: translateX(140%);
  transition: .5s;
  z-index: 20;
  width: calc(100% - 30px);
  &.active{
    transform: translateX(0);
  }
}





@media(max-width: 830px){
  .mobile__menu{
    position: fixed;
    z-index: 1000;
    bottom: 0;
    left: 0;
    width: 100%;
    display: block;
    padding-top: 20px;
    padding-bottom: 15px;
    box-shadow: 0px 0px 9px rgba(0, 0, 0, 0.1);
    background-color: #fff;
    .container{
      display: flex;
      justify-content: space-between;
    }
    .header__basket{
      transform: translateY(-8px);
    }
  }

  .mobile__menu-link{
    text-align: center;
  }
  .header.--desktop{
    display: none;
  }
  .header.--mobile{
    display: block;
    padding-top: 15px;
    .--flex{
      flex-wrap: nowrap;
    }
  }

  .header__city{
    color: #00A7CD;
    b{
      font-weight: bold;
    }
  }

  .header__logo{
    max-width: 128px;
    margin-right: 0px;
  }

  .header__link{
    margin-right: 15px;
    &:last-child{
      margin-right: 0px;
    }
    svg{
      margin-right: 0px;
    }
  }

  .mobile__burger{
    margin-left: 5px;
  }

  .header__enter{
    width: 100%;
    color: #fff;
    justify-content: center;
    margin-top: 25px;
    margin-bottom: 20px;
  }

  .nav{
    flex-wrap: wrap;
    justify-content: center;
    margin-left: -10px;
    margin-right: -10px;
  }

  .header__search{
    margin-bottom: 20px;
    margin-right: 0px;
  }

  .nav__link{
    margin-right: 8px;
    margin-left: 8px;
    margin-bottom: 15px;
    font-size: 14px;
    
  }
  .header__city {
    display: flex;
    align-items: center;
    font-size: 14px;
    svg{
      margin-left: 10px;
    }
  }
}


@media(max-width: 410px){

  .header__logo{
    max-width: 110px;
  }

  .header__city{
    font-size: 11px;
  }
}